


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0 3%; /* Adjusted padding for better responsiveness */
}


@media (max-width: 768px) {
    body {
        margin: 0 2%;
    }
}


header {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 0.8rem; /* Adjusted padding for smaller screens */


@media (max-width: 768px) {
    header {
        padding: 0.5rem;
    }
}

    background-color: #f7fafc; /* Assuming bg-gray-100 */
    
    @media (max-width: 768px) {
        padding: 0.5rem; /* Further reduced padding for smaller screens */
    }

    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1); /* Assuming shadow-md */
}


select {
    font-size: 1rem; 
    width: auto; /* Adjusted width for better responsiveness */

    padding: 0.5rem;
    margin: 0 0.5rem;
    border: 1px solid #d1d5db;
    border-radius: 0.25rem;
    background-color: #ffffff;
    width: 100%; /* Make select responsive */
}

@media (max-width: 768px) {
    select {
        font-size: 0.9rem; /* Adjust font size for smaller screens */
    }
}



select option {
    @apply text-base p-2;
}

input[type="text"] {
    font-size: 1rem; 
    width: auto; /* Adjusted width for better responsiveness */

    padding: 0.5rem;
    border: 1px solid #d1d5db;
    border-radius: 0.25rem;
    width: 100%; /* Make input responsive */
}

@media (max-width: 768px) {
    input[type="text"] {
        font-size: 0.9rem; /* Adjust font size for smaller screens */
    }
}
